
body {
  display: flex;
  flex-direction: column;
  background-color: lightblue;
  font-family: 'Press Start 2P', monospace;
  line-height: 25px;
}

body, .root-layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.itsweird {
  margin-left: -10px;
}

p {
  line-height: 25px;
}

a.active {
  background-color: blue;
}

a {
  padding: 10px;
}


#header {
  width: 100%;
  background-color: #333;
  font-size: 20px;
  color: #fff;
  padding: 20px;
}



#header h1 {
  padding-left: 250px;
}

#content {
  display: flex;
  flex-grow: 1;
}



#sidebar {
  width: 300px;
  height: 100%; /* Adjust based on the height of your header */
  font-size: 30px;
  background-color: #333;
  padding: 20px;
}

#sidebar ul {
  list-style-type: none;
}

#sidebar ul li {
  padding-top: 20px;
}

#sidebar a{
  padding: 10px;
}

#main-content {
  padding-top: 20px;
  flex-grow: 1;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 70px;
}


iframe {
  padding-top: 20px;
}


.imgmakeitsmaller {
  width: 600px;
  padding-top: 20px;
}

#mobileapppic {
  width: 200px;
  padding-left: 10px;
  padding-top: 20px;
}

.trevorswebsitepics {
  display: flex;
  flex-direction: row;
  align-items: flex-start; 
}

#musicdiv {
  margin-left: 0px;
  padding-left: 0px;
}

/*-------------------SIDEBAR----------------------*/

.spacer {
  flex-grow: 1;
}


.sidebar-container {
  margin-right: -80px;
}

.sidebar {
  overflow-y: auto; /* introduces a scrollbar if the content overflows */
  direction: rtl; /* places the scrollbar on the left side */
  font-size: 20px;
  background-color: #333;
}

#smlnav {
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100vw;
  display: flex;
  padding-left: 10px;
  justify-content: space-around;
  background-color: #333;
  display: none;
}

#smlnav a {
  padding-left: 10px;
  color: white;
}


.sidebar ul {
  list-style-type: none;
  display: block;
  padding-right: 20px;
  padding-top: 10px;
  padding-left: 30px;
}

.sidebar ul li {
  padding-right: 30px;
  margin-left: -5px;
  padding-top: 10px;
}

.sidebar ul a {
  color: #fff;
}

.invisible-div {
  width: 300px;
  visibility: hidden;
  display: block;
}


.sidebar-content {
  line-height: 30px;
  direction: ltr; /* resets the text direction for the sidebar content */
}

.sidebar-button {
  width: 40px; /* Adjust this to change the space between lines */
  height: 25px; /* Adjust this to change the space between lines */
  padding-top: 2px;
  display: none; /* initially hidden */
  flex-direction: column;
  justify-content: space-between; 
  align-items: center;
  cursor: pointer;
  border: none;
  background: lightgrey; /* light grey background */
  transform: rotate(90deg);
  position: fixed; /* fixed position */
  left: -7.5px; /* touch the left side of the screen */
  top: 50%; /* vertically centered */
  z-index: 1000; /* elevate above other content */
}

.sidebar-button .line {
  width: 100%; /* Adjust this to change the length of the lines */
  height: 2px;
  background-color: #000; 
}

body.no-scroll {
  overflow: hidden;
}

.sidebar.active {
  padding-top: 15px;
  display: block; /* display when active class is added */
  z-index: 2000;
  width: 150px;
  position: fixed; /* fixed position */
  top: 0; /* positioned at the top */
  bottom: auto; /* allow it to adjust to the content */
  max-height: 100vh;
  overflow-y: auto; /* enable scroll when content overflows */
}

.sidebar.active {
  padding-right: 50px;
}

.description {
  min-width: 475px;
  max-width: 1000px;
  padding-right: 30px;
}


/*-------------------SIDEBAR----------------------*/

@media (max-width: 1080px) {
#mobileapppic {
  width: 300px;
  padding-left: 0px;
  padding-top: 20px;
}

.trevorswebsitepics {
  display: block;
}

.trevorswebsitepics img {
  display: block;
}


}

@media (max-width: 867px) {
  .imgmakeitsmaller {
    width: 90%;
  }
}

@media (max-width: 768px) {
  #header {
    justify-content: center;
  }
  
  #header h1 {
    padding-left: 40px;
  }

  .sidebar-container {
    display: none;
  }

  #smlnav {
    display: flex;
    padding-left: 60px;
    justify-content: space-around;
    background-color: #333;
    display: block;
  }
}

@media (max-width: 580px) {
  #smlnav {
    padding-left: 30px;
  }
}

@media (max-width: 558px) {
  .imgmakeitsmaller {
    width: 400px;
  }
}

@media (max-width: 400px) {
  #smlnav {
    max-width: 390px;
    padding-left: 0px;
    padding-bottom: 10px;
    font-size: 10px;
    margin: 0px;
  }
  #smlnav a {
    padding-left: 20px;
  }
  #header {
    max-width: 100vw;
    padding-right: 0px;
    padding-left: 0px;
    margin-right: 0px;
    margin-left: 0px;
    font-size: 15px;
  }
  body {
    max-width: 100vw;
  }
  #main-content {
    max-width: 370px;
    padding-right: 0px;
    padding-left: 15px;
  }

  #content {
    max-width: 375px;
  }
  .imgmakeitsmaller {
    width: 95%;
    padding-left: 0px;
    margin-left: 0px;
  }

  #mobileapppic {
    padding-left: 10px;
  }
  #endnavcontainer {
    max-width: 375px;
  }

  .description {
    min-width: 0px;
    max-width: 375px;
    padding-right: 10px;
    padding-left: 0px;
  }
  #root {
    max-width: 390px;
  }

  .itsweird {
    margin-left: -10px
  }
}


/*-------------------EndBar----------------------*/

.navs {
  background-color: #333;
  font-family: monospace;
}

.border {
	display: grid;
	place-items: center;
	min-height: 20px;
	padding: 0.5rem;
}

.endnavcontainer {
  margin-top: -20px;
  width: 100vw;
}

.endbanner {
  position: static;
  margin: 0 auto;
  width: 100%;
  padding-left: 0px;
  display: grid;
  align-content: stretch;
  justify-content: stretch;
}

.endbanner p {
  color: white;
  font-family: 'Press Start 2P', monospace;
  font-size: 10px;
  margin: 0;
  padding-left: 20px;
  padding-right: 20px;
}


/*-------------------EndBar----------------------*/


